import shuffledGreen from "./shuffledGreen";
import shuffledBrown from "./shuffledBrown";
import shuffledBlue from "./shuffledBlue";

let arrayBlueVeryHard = [];
let arrayGreenVeryHard = [];
let arrayBrownVeryHard = [];

function azathotGreenVeryHard() {
  for (let i = 0; i < shuffledGreen.length; i++) {
    if (shuffledGreen[i].difficulty == "hard") {
      arrayGreenVeryHard.push(shuffledGreen[i]);
    }
  }
  //console.log(arrayGreenVeryHard);
}
azathotGreenVeryHard ()

function azathotBlueVeryHard() {
  for (let i = 0; i < shuffledBlue.length; i++) {
    if (shuffledBlue[i].difficulty == "hard") {
      arrayBlueVeryHard.push(shuffledBlue[i]);
    }
  }
  for (let i = 0; i < shuffledBlue.length; i++) {
    if (arrayBlueVeryHard.length > 2) {
      arrayBlueVeryHard.pop(shuffledBlue[i]);
    }
  }
 // console.log(arrayBlueVeryHard);
}
azathotBlueVeryHard ()

function azathotBrownVeryHard() {
  for (let i = 0; i < shuffledBrown.length; i++) {
    if (shuffledBrown[i].difficulty == "hard") {
      arrayBrownVeryHard.push(shuffledBrown[i]);
    }
  }
  for (let i = 0; i < shuffledBrown.length; i++) {
    if (shuffledBrown[i].difficulty == "normal") {
      arrayBrownVeryHard.push(shuffledBrown[i]);
    } else if (arrayBrownVeryHard.length === 9) {
      break;
    } else if (arrayBrownVeryHard.length > 9) {
      arrayBrownVeryHard.pop(arrayBrownVeryHard[i]);
    }
  }
  //console.log(arrayBrownVeryHard);
}
azathotBrownVeryHard ()

