import shuffledGreen from "./shuffledGreen";
import shuffledBrown from "./shuffledBrown";
import shuffledBlue from "./shuffledBlue";

let arrayBlueHard = [];
let arrayGreenHard = [];
let arrayBrownHard = [];

//BROWN
function azathotBrownHard() {
  for (let i = 0; i < shuffledBrown.length; i++) {
    let result = shuffledBrown.filter(word => word.difficulty !== 'easy');
    if (arrayBrownHard.length < 9){
      arrayBrownHard.push(result[i])
    }
  }
  console.log(arrayBrownHard);
}
//azathotBrownHard ()
//BROWN

//BLUE
function azathotBlueHard() {
  for (let i = 0; i < shuffledBlue.length; i++) {
    let result = shuffledBlue.filter(word => word.difficulty !== 'easy');
    if (arrayBlueHard.length < 2){
      arrayBlueHard.push(result[i])
    }
  }
  console.log(arrayBlueHard);
}
//azathotBlueHard ()
//BLUE

//GREEN
