//CARDS
import shuffledGreen from "./shuffledGreen";
import shuffledBrown from "./shuffledBrown";
import shuffledBlue from "./shuffledBlue";
//CARDS

let arrayBlueEasy = [];
let arrayGreenEasy = [];
let arrayBrownEasy = [];

//BROWN
function azathotBrownEasy() {
  for (let i = 0; i < shuffledBrown.length; i++) {
    if (
      shuffledBrown[i].difficulty == "easy" ||
      shuffledBrown[i].difficulty == "normal"
    ) {
      arrayBrownEasy.push(shuffledBrown[i]);
    }
  }
  for (let j = 0; j < arrayBrownEasy.length; j++){
  if (arrayBrownEasy.length > 9){
    arrayBrownEasy.pop(arrayBrownEasy[j])
  }
}
  console.log(arrayBrownEasy);
}
//azathotBrownEasy ()
//BROWN

//BLUE
function azathotBlueEasy() {
  for (let i = 0; i < shuffledBlue.length; i++) {
  let result = shuffledBlue.filter(word => word.difficulty !== 'hard');
  if (arrayBlueEasy.length < 2){
    arrayBlueEasy.push(result[i])
  }
}
console.log(arrayBlueEasy)
}
//azathotBlueEasy ()
//BLUE

//GREEN
function azathotGreenEasy() {
  for (let i = 0; i < shuffledGreen.length; i++) {
    let result = shuffledBlue.filter(word => word.difficulty !== 'hard');
    if (arrayGreenEasy.length < 5){
      arrayGreenEasy.push(result[i])
    }
  }
  console.log(arrayGreenEasy);
}
