mixUp.addEventListener("click", (e) => {
  if (e.target.classList.contains("mixUpInitVeryEasy")) {
    mixUp.classList.add("activeMixClickVeryEasy");
    stupidCards.style.opacity = 1;
    mythicCardBackground.style.opacity = 1;
  }
})
