const veryEasy = document.querySelector('.diffVeryEasy')

let arrayBlueVeryEasy = [];
let arrayGreenVeryEasy = [];
let arrayBrownVeryEasy = [];

function azathotGreenVeryEasy() {
  for (let i = 0; i < shuffledGreen.length; i++) {
    if (shuffledGreen[i].difficulty == "easy") {
      arrayGreenVeryEasy.push(shuffledGreen[i]);
    }
  }
  //console.log(arrayGreenVeryEasy);
}
azathotGreenVeryEasy ()

function azathotBlueVeryEasy() {
  for (let i = 0; i < shuffledBlue.length; i++) {
    if (shuffledBlue[i].difficulty == "easy") {
      arrayBlueVeryEasy.push(shuffledBlue[i]);
    }
  }
  for (let i = 0; i < shuffledBlue.length; i++) {
    if (arrayBlueVeryEasy.length > 2) {
      arrayBlueVeryEasy.pop(shuffledBlue[i]);
    }
  }
  //console.log(arrayBlueVeryEasy);
}
azathotBlueVeryEasy ()

function azathotBrownVeryEasy() {
  for (let i = 0; i < shuffledBrown.length; i++) {
    if (shuffledBrown[i].difficulty == "easy") {
      arrayBrownVeryEasy.push(shuffledBrown[i]);
    }
  }
  for (let i = 0; i < shuffledBrown.length; i++) {
    if (shuffledBrown[i].difficulty == "normal") {
      arrayBrownVeryEasy.push(shuffledBrown[i]);
    } else if (arrayBrownVeryEasy.length === 9) {
      break;
    } else if (arrayBrownVeryEasy.length > 9) {
      arrayBrownVeryEasy.pop(arrayBrownVeryEasy[i]);
    }
  }
  //console.log(arrayBrownVeryEasy);
}
azathotBrownVeryEasy ()

